body {
	background: #282c34;
}
.App {
}

.App-logo {
  height: 100px;
  pointer-events: none;
  display: inline-block;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  height: 100px;
  width: 100%;
  font-size: calc(10px + 2vmin);
  color: white;
  display: inline-block;
}

.App-header h1 {
	display: inline-block;
	top: -30px;
	height: 100%;
	position: relative;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

a {
	color: white;
}

.flex-container {
	display: flex;
	flex-direction: row;
	flex-wrap: nowrap;
	justify-content: normal;
	align-items: normal;
	align-content: normal;
	
	.flex-items {
		display: block;
		flex-grow: 1;
		flex-shrink: 1;
		flex-basis: 1;
		align-self: center;
		order: 0;
		text-align: center;
	}
}

@media (max-width: 700px) {
	.flex-container {
	  flex-direction: column;
	}
  }

a.resource {
	background: #3e4450;
	padding: 20px;
	margin: 30px;
	border: 1px solid #333;
	border-radius: 10px;
	max-width: 33vw;

	img {
		height: 50px;
	}
}

a.resource:hover{
	background: #737d93;
}

#ttrs {
	background: #9400FF

